@use "../util" as *;

.cards {
    display: grid;
    grid-template-columns: 1fr;
    gap: rem(30);

    @include breakpoint(medium) {
        grid-template-columns: repeat( 2, 1fr);
        
    }

    @include breakpoint(large) {
        grid-template-columns: repeat( 4, 1fr);
        
    }
}

.card {
    position: relative;
    overflow: hidden;;
  background: var(--dark-card);
  color: var(--dark-text1);
  text-align: center;
  padding: rem(25);
  border-radius: rem(5);

  &--facebook {
    border-top: rem(5) solid var(--facebook);
  }

  &--twitter {
    border-top: rem(5) solid var(--twitter);
  }

  &--instagram {
padding-top: rem(30);
    &::before {
        content: '';
        position: absolute;
        display: block;
        left: 0;
        top: 0;
        height: rem(5);
        width: 100%;
        background: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
       
    }
  }

  &--youtube {
    border-top: rem(5) solid var(--youtube);
  }

  &__platform {
    display: flex;
    justify-content: center;
    align-items: center;
    height: rem(20);
    margin-top: rem(5);
    margin-bottom: rem(28);
  }

  &__icon {
    margin-right: rem(12);

    &--facebook {
    }

    &--twitter {
    }

    &--instagram {
    }

    &--youtube {
    }
  }

  &__username {
    font-size: rem(12);
    font-weight: bold;
  }

  &__followers {
    margin-bottom: rem(25);
  }

    &__count {
      color: var(--dark-text2);
      font-weight: bold;
      margin-bottom: rem(4);
      letter-spacing: rem(-2);
      line-height: 1;

      &--big {
        font-size: rem(56);
      }

      &--small {
      }
    }

    &__label {
      color: var(--dark-text1);
      font-size: rem(12);
      text-transform: uppercase;
      letter-spacing: rem(5);
      font-weight: 400;
      
    }
  

  &__change {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: rem(12);;
    &--up {
        color: var(--limegreen);
    }

    &--down {
        color: var(--brightred);
    }

    img {
        margin-right: rem(4);
    }
  }

  &__number {
  }
}
